.hero{
    height:100vh;
    background:linear-gradient(180deg,#fde1ff,#e1ffea22 60%);
    display: flex;
}
.hero-left{
    flex-direction: column;
    flex: 1;
    display: flex;
    justify-content: center;
    gap:20px;
    padding-left: 180px;
    line-height: 1.1;
}

.hero-left p{
    color:#171717;
    font-size: 100px;
    font-weight: 700;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap:20px;
}
.hero-hand-icon img{
    width: 105px;
}

.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items:center;
    gap: 15px;
    width:310px;
    height:70px;
    margin-top: 30px;
    background-color: #ff4141;
    Color:white;
    font-size: 22px;
    font-weight: 500;

}

.hero-rigth{
    flex:1;
    display: flex;
    align-items:center;
    justify-content: center;
}